import './App.css';
import React, { useState,useEffect } from 'react';
let arr = [];
function blockEvent(e) {
  const input = document.querySelector('.text-input');
  e.preventDefault();
  createList( input.value );
}

function createList( text ) {
  const input = document.querySelector('.text-input');
  const ulTage = document.querySelector('.to-do-ul'); 
  const liTag = document.createElement('li');
  const spanTage = document.createElement('span');
  const btn = document.createElement('button');
  spanTage.innerText = text;
  btn.innerText = '삭제';
  liTag.id = arr.length;
  liTag.appendChild(spanTage);
  liTag.appendChild(btn);
  ulTage.appendChild(liTag);
  let obj = {
    id : arr.length,
    content : text
  }
  arr.push(obj);
  storage();
  input.value = ''; 
  btn.addEventListener('click',del);
}

function storage() {
  localStorage.setItem('item',JSON.stringify(arr));
}

function del() {
  let parent = this.parentNode;
  parent.remove();
  let newarr = arr.filter((el) => {
     return parseInt(parent.id) !== el.id;
  })
  arr = newarr;
  storage();
}

function firstStart() {
  const getstorage = localStorage.getItem('item');
  if( getstorage !== null ){
    let storageval = JSON.parse(getstorage);
    storageval.map((el) => {
      createList( el.content );
    })
  }
}

function App() {
  useEffect(() => {
    firstStart();
  })
  return (
    <div className="App">
      <form action="" className="formatag" onSubmit={blockEvent}>
        <input type="text" className="text-input"/>
      </form>
      <ul className="to-do-ul"></ul>
    </div>
  );
}

export default App;
